import React from 'react';

const Feedback = () => (
  <div>
    <h3> </h3>
    <div className="feedback-form">
      <form className="feedback-form__inner" action="/" method="POST">
        <div className="feedback-form__section">
          <label className="feedback-form__label" htmlFor="name"> *</label>
          <input className="feedback-form__input" id="name" required type="text" placeholder="" />
        </div>
        <div className="feedback-form__section">
          <label className="feedback-form__label" htmlFor="email">Email *</label>
          <input className="feedback-form__input" id="email" required type="text" placeholder="Email" />
        </div>
        <div className="feedback-form__section">
          <label className="feedback-form__label" htmlFor="message"> *</label>
          <textarea className="feedback-form__input feedback-form__textarea" name="message" id="message" placeholder="" />
        </div>
        <div className="feedback-form__section">
          <label className="feedback-form__label" htmlFor="file">  *</label>
          <input name="file" type="file" />
        </div>
        <div className="feedback-form__buttons">
          <button className="feedback-form__button" type="submit"></button>
          <button className="feedback-form__button" type="reset"></button>
        </div>
      </form>
    </div>
  </div>
);

export default Feedback;



